<template>
    <div class="wiki-modify-warp">
        <mineheader></mineheader>
        <div class="wiki-modify-main">
            <ul>
                <li>
                    <el-tooltip class="item" effect="dark" content="点击更换头像" placement="right-start">
                    <el-upload
                        ref="imgFile"
                        class="avatar-uploader"
                        action='http://192.168.68.214:50000/post/file'
                        name="avatar"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload"
                        >
                        <img v-if="imageUrl" :src="imageUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    </el-tooltip>
                    <p v-show="!modifyUsername">
                        <el-input
                            type="text"
                            placeholder="请输入内容"
                            v-model="username"
                            maxlength="10"
                            show-word-limit
                        >
                        </el-input>
                        <i class="el-icon-circle-check" @click="modifyUsernameState_true"></i>
                    </p>
                    <p v-show="modifyUsername">{{people['username']}} <i class="el-icon-edit" @click="modifyUsernameState_false"></i></p>
                </li>
                <li>    
                    <span class="wiki-modify-main-select">性别:</span>
                    <el-radio v-model="radio" label="0">保密</el-radio>
                    <el-radio v-model="radio" label="1">男</el-radio>
                    <el-radio v-model="radio" label="2">女</el-radio>
                </li>
                <li>    
                    <span class="wiki-modify-main-select">个性签名:</span>
                    <el-input
                        type="text"
                        placeholder="请输入内容"
                        v-model="text"
                        maxlength="20"
                        show-word-limit
                        >
                    </el-input>
                </li>
                <!-- <li>    
                    <span class="wiki-modify-main-select">年龄:</span>
                    <el-form :model="numberValidateForm" ref="numberValidateForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item
                            prop="age"
                            :rules="[
                                { required: true, message: '年龄不能为空'},
                                { type: 'number', message: '年龄必须为数字值'}
                            ]"
                        >
                            <el-input 
                                type="age" 
                                v-model.number="numberValidateForm.age" 
                                auto-complete="off"
                                @input="ageLength"
                            >
                            </el-input>
                        </el-form-item>
                    </el-form>    
                </li> -->
                <li>    
                    <span class="wiki-modify-main-select">所在省份:</span>
                         <el-select v-model="value6" placeholder="请选择">
                            <el-option
                            v-for="item in cities"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            <span style="float: left">{{ item.label }}</span>
                            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
                            </el-option>
                        </el-select>
                </li>
                <li>    
                    <span class="wiki-modify-main-select">邮箱:</span>
                    <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic">
                            <el-form-item
                                prop="email"
                                :rules="[
                                { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                                { type: 'email', message: '请输入正确的邮箱格式', trigger: ['blur', 'change'] }
                                ]"
                                class="wiki-modify-main-select-email"
                            >
                            <el-input v-model="dynamicValidateForm.email"></el-input>
                        </el-form-item>
                    </el-form>
                </li>
            </ul>

            <el-button type="info" @click="submissionMsg">提交信息</el-button>
        </div>
    </div>
</template>

<script>
import mineheader from '@/components/publicHeader' //公共头
import {mapState, mapMutations} from 'vuex'
import * as ajax from '@/api'

export default {
    components:{
        mineheader
    },
    data(){
        return{
            radio: '0', //性别
            username: '',
            numberValidateForm: {
                age: null
            },
            cities: [{
                value: 'Beijing',label: '北京'}, {value: 'Shanghai',label: '上海'}, {value: 'Tianjin',label: '天津'}
                , {value: 'Jiangsu',label: '江苏'}, {value: 'Zhejiang',label: '浙江'}, {value: 'Anhui',label: '安徽'
                },{value: 'Fujian',label: '福建'}, {value: 'Jiangxi',label: '江西'}, {value: 'Hunan',label: '湖南'
                }, {value: 'Shandong',label: '山东'}, {value: 'Henan',label: '河南'},{value: 'Neimeng',label: '内蒙古自治区'
                }, {value: 'Hubei',label: '湖北'}, {value: 'Ningxia',label: '宁夏回族自治区'}, {value: 'Guangdong',
                label: '广东'}, {value: 'Xizang',label: '西藏自治区'},{value: 'Hainan',label: '海南'}, {value: 'Guangxi',
                label: '广西壮族自治区'}, {value: 'Sichuan',label: '四川'}, {value: 'Hebei',label: '河北'}, {value: 'Guizhou',
                label: '贵州'}, {value: 'Chongqing',label: '重庆'},{value: 'Shanxi',label: '山西'}, {value: 'Yunnan',
                label: '云南'}, {value: 'Liaoning',label: '辽宁'}, {value: ' Shanxi',label: '陕西'}, {value: 'Jilin',
                label: '吉林'}, {value: 'Gansu',label: '甘肃'},{value: 'Heilongjiang',label: '黑龙江'}, {value: 'Qinghai',
                label: '青海'}, {value: 'Taiwan',label: '台湾'}, {value: 'Xianggang',label: '香港特别行政区'}, {value: 'Aomen',
                label: '澳门特别行政区'}
            ],
            value6: '湖南',
            dynamicValidateForm: {
                domains: [{
                    value: ''
                }],
                email: ''
            },
            imageUrl: '',
            modifyUsername: true,
            text:'',
            headers: 'JWT ' + localStorage.getItem('token')
        }
        
    },
    methods:{
        ...mapMutations(['modifypeoplemsg']),
        ageLength(){
            this.numberValidateForm.age>99 ? this.numberValidateForm.age= 99 : this.numberValidateForm.age
        },
        handleAvatarSuccess(res, file) {
            this.imageUrl = res.data.file_data
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/png' || file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG) {
            this.$message.error('上传头像图片只能是 JPG 或者 PNG格式!');
            }
            if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        },
        submissionMsg(){
            var reg = new RegExp("^[a-z0-9A-Z]+[- | a-z0-9A-Z . _]+@([a-z0-9A-Z]+(-[a-z0-9A-Z]+)?\\.)+[a-z]{2,}$")
            var sex = ''
            if(this.radio == '0'){
                sex = '保密'
            }else if(this.radio == '1'){
                sex = '男'
            }else{
                sex = '女'
            }

            if(!reg.test(this.dynamicValidateForm.email)){
                alert('邮箱格式不对，请输入正确的邮箱！')
            }else{
                ajax.modifyMsg(this.people["username"],sex,this.value6,this.text,this.imageUrl,this.dynamicValidateForm.email).then((resp)=>{
                    if(resp.data.code == 1){
                        alert('修改信息成功')
                        var data = {
                            "username": resp.data.data.user_info.username,
                            "email": resp.data.data.user_info.email,
                            "gender": resp.data.data.user_info.gender,        //性别
                            "scores": resp.data.data.user_info.scores,        //用户积分
                            "signature": resp.data.data.user_info.signature,  //个性签名
                            "city": resp.data.data.user_info.city,
                            "avatar": resp.data.data.user_info.avatar         //头像
                        }   
                        this.modifypeoplemsg(JSON.stringify(data))
                    }
                })
                this.$router.push('/mine')
            }
        },
        modifyUsernameState_false(){
            this.modifyUsername = !this.modifyUsername; //显示修改名字框
        },
        modifyUsernameState_true(){
            var sex = ''
            if(this.radio == '0'){
                sex = '保密'
            }else if(this.radio == '1'){
                sex = '男'
            }else{
                sex = '女'
            }
            ajax.modifyMsg(this.username,sex,this.value6,this.text,this.imageUrl,this.dynamicValidateForm.email).then((resp)=>{
                if(resp.data.code == 1){
                    alert('修改信息成功')
                    var data = {
                    "username": resp.data.data.user_info.username,
                    "email": resp.data.data.user_info.email,
                    "gender": resp.data.data.user_info.gender,        //性别
                    "scores": resp.data.data.user_info.scores,        //用户积分
                    "signature": resp.data.data.user_info.signature,  //个性签名
                    "city": resp.data.data.user_info.city,
                    "avatar": resp.data.data.user_info.avatar         //头像
                    }   
                    this.modifypeoplemsg(JSON.stringify(data))
                    }
            })
        }
    },
    computed:{
        ...mapState(['people'])
    },
    created(){
        if(this.people.gender == '男'){
            this.radio = "1"
        }else if(this.people.gender == '女'){
            this.radio = "2"
        }else{
            this.radio = "0"
        } //判断性别
        this.dynamicValidateForm.email = this.people.email  // 邮箱
        this.value6 = this.people.city          //城市
        this.username = this.people.username  //用户名
        this.text = this.people.signature //签名
        this.imageUrl = JSON.parse(localStorage.getItem('people')).avatar
    }

}
</script>

<style lang='scss'>
    html,body{
        width: 100%;
        height: 100%;
            .el-tooltip__popper {
                top: 226px !important;
                left: 925px !important;
            }
    }
    
    .wiki-modify-warp{
        width: 100%;
        height: 100%;
        background-color: #f6f6f6;
        .wiki-modify-main{
            width: 1280px;
            height: auto;
            margin: 0 auto;
            padding: 100px 200px 0 200px;
            .avatar-uploader{
                width: 150px;
                height: 150px;
                .el-upload{
                    width: 150px;
                    height: 150px;
                    background-color: #fff;
                    position: relative;
                    .el-icon-plus:before{
                        position: absolute;
                        margin: auto;
                        top: 66px;
                        left: 69px;
                    }
                    img{
                        width: 150px;
                        height: 150px;
                    }
                }
            }
            
            ul{
                width: 100%;
                list-style: none;
                li:nth-child(1){
                    height: 165px;
                    p{
                        position: absolute;
                        top: -42px;
                        left: 202px;
                        font-size: 30px;
                        .el-input{
                            width: 100%;
                        }
                        .el-icon-circle-check{
                            position: absolute;
                            top: 66px;
                            left: 214px;
                        }
                    };
                }
                li{
                    padding:0 20px;
                    width: 100%;
                    height: 100px;
                    line-height: 150px;
                    border-bottom: 1px solid #999;
                    font-weight:bold;
                    position: relative;
                    .wiki-modify-main-select{
                        margin-right: 80px;
                    }
                    .el-form{
                        position: absolute;
                        top: 55px;
                        left: 69px;
                        .el-form-item__content{
                            width: 100%;
                            .el-form-item__error{
                                position: absolute;
                                left: 210px;
                                top: 10px;
                                width: 130px;
                            }
                            .wiki-modify-main-select-email{
                                left: 10px; //邮箱错误信息
                            }
                        }
                        .el-input__inner{
                            width: 200px;
                        }

                    }
                    .el-input{
                        width: 60%;
                    }
                }
            }
            .el-button{
                margin: 50px 390px;
            }
        }
    }
    .el-select-dropdown{
        top: 386px !important;
    }
</style>
